.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.arab {
  background-color: white;
  color: black;
  font-size: 40px;
}

.Dashboard {
  margin: 30px;
}

.App ul li {
  text-align: center;
  margin: 0 auto;
  list-style: none;
}

.Question {
  margin: 0 auto;
  color: black;
  font-size: 80px;
  background-color: white;
  max-width: 150px;
  min-height: 120px;
  border-radius: 30px;
}

.LessonButtonBox {
  border-radius: 10px;
  background-color: lightgrey;
  border: none;
  color: black;
  padding: 15px 32px;
  margin: 5px;
  text-align: center;
  text-decoration: none;
  display: block;
  font-size: 20px;
  font-weight: bold;
}
.LessonButtonBox:hover {
  color: darkorange;
  background-color: darkblue;
}
.ArticleButtonBox {
  border-radius: 5px;
  background-color: lightgrey; /* Green */
  border: none;
  color: black;
  padding: 15px 32px;
  margin: 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
}
.ArticleButtonBox:hover {
  color: white;
  background-color: black;
}
.TopBar {
  font-weight: bold;
  position:fixed;
  left: 0;
  top: 0;
  color:black;
  background-color: lightgrey;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 50px;
  display: block;
  font-size: 15px;
  padding: 10px;
}

article {
  min-height: 150px;
}

.QuestionBox {
  border-radius: 5px;
  background-color: lightgrey; /* Green */
  border: none;
  color: black;
  padding: 15px 32px;
  margin: 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
}
.QuestionBox:hover {
  color: white;
  background-color: darkred;
}
